.hero {
  &__inner {
    display: flex;
    align-items: center;
  }
  &__content {
    max-width: 567px;
  }
  .hero__title {
    font-size: 68px;
    line-height: 74px;
    margin-bottom: 22px;
  }
  .hero__text {
    margin-bottom: 34px;
  }
  .hero__btn{
    padding: 26px 38px;
    font-family: var(--font-bold, sans-serif);
    font-weight: 700;
    font-size: 18px;
    line-height: 20px;
  }
  &__contacts {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__phone {
    font-weight: 700;
    font-size: 28px;
    line-height: 36px;
    font-family: var(--font-bold, sans-serif);
    color: var(--dark-color);
    position: relative;
    padding-left: 88px;
    transition: all .3s;
    &::before {
      content: "";
      position: absolute;
      background-image: url(../../img/phone.svg);
      background-repeat: no-repeat;
      width: 72px;
      height: 72px;
      left: 0;
      top: -7px;
    }
    &:hover{
      opacity: 0.76;
      transform: translate3d(0px, -3px, 0.01px);
    }
  }
  .hero__phone-text {
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 1.6px;
    text-transform: uppercase;
    font-family: var(--font-family, sans-serif);
  }
  &__image {
    transform: translateX(110px);
  }
}
