.works{
  &__inner{
    padding: 240px 0;
    border-bottom: 1px solid #DCDDEB;
  }
  &__title{
    text-align: center;
    margin-bottom: 21px;
  }
  &__text{
    text-align: center;
    max-width: 648px;
    margin: 0 auto;
    margin-bottom: 58px;
  }
  &__items{
    display: flex;
    justify-content: space-between;
    margin-bottom: 56px;
  }
  &__item{
    text-align: center;
  }
  &__image{
    margin-bottom: 40px;
  }
  &__item-title{
    font-size: 28px;
    line-height: 36px;
    margin-bottom: 16px;
  }
  &__links{
    display: flex;
    justify-content: center;
    gap: 28px;
  }
  &__link{
    padding: 26px 38px;
  }
  &__explore{
    background-color: var(--light-color);
    color: var(--dark-color);
    border: 1px solid #EFF0F6;
    box-shadow: 0px 4px 10px rgba(20, 20, 43, 0.04);
    transition: all .3s;
    font-family: var(--font-family, sans-serif);
    font-weight: 400;
    &:hover{
      background-color: var(--accent-color);
      color: var(--light-color);
    }
  }
}
