.header {
  // position: fixed;
  // top: 0;
  // left: 0;
  // width: 100%;
  // background-color: #fff;
  // z-index: 10;
  // box-shadow: 0px 4px 18px rgb(0 0 0 / 15%);
  &__inner {
    padding: 32px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right-cart{
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    margin-right: 24px;
  }
  .logo {
    margin-right: 40px;
    transition: all 0.3s;
    &:hover {
      transform: scale3d(0.96, 0.96, 1.01);
    }
  }
  .nav {
    &__list {
      display: flex;
    }
    &__item {
      &:not(:last-child) {
        margin-right: 22px;
      }
    }
    &__link {
      color: var(--dark-color);
      font-weight: 400;
      font-size: 18px;
      line-height: 30px;
      transition: all 0.3s ease;
      &:hover {
        color: var(--accent-color);
      }
    }
  }
  .btn{
    font-family: var(--font-family, sans-serif);
    padding: 18px 24px;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
  }
}
