// colors
:root {
  // base
  --font-family: "RobotoRegular", sans-serif;
  --font-bold: "RobotoBold", sans-serif;
  --content-width: 1268px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --text-color: #6e7191;
  --dark-color: #211f54;
  --accent-color: #0075ff;
  --hover-color: #1d53bb;
}
