.btn {
  display: inline-block;
  border: none;
  background-color: var(--accent-color);
  border-radius: 14px;
  color: var(--light-color);
  padding: 26px 38px;
  font-family: var(--font-bold, sans-serif);
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  transition: all 0.3s;
  &:hover {
    background-color: var(--hover-color);
    transform: translate3d(0px, -3px, 0.01px);
  }
}


